<template>
  <div>
    <!-- & -->
    <section class="toolbar-panel">
      <!--  -->
      <form-create v-model="fApi" :rule="formRules" :option="formOptions"></form-create>
      <!--  -->
      <div class="toolbar-item">
        <el-button type="primary" @click="_handleSearch"></el-button>
        <el-button @click="_handleReset"></el-button>
        <!-- solt -->
        <slot name="filterHandle"></slot>
      </div>
    </section>

    <!-- solt -->
    <section v-if="isShowStatistics" class="toolbar-totaltip mb-10">
      <slot name="statistics"></slot>
    </section>

    <!--  -->
    <section>
      <el-table :data="tableData" v-bind="$attrs" v-on="$listeners" style="width: 100%;">
        <slot v-if="selection" name="slotSelection"></slot>
        <el-table-column v-for="(item, index) in columns" :key="index" v-bind="item || {}">
          <template slot="header" slot-scope="scope">
            <span :class="item.class">{{ scope.column.label }}</span>
          </template>
          <template slot-scope="scope">
            <!-- slotedit -->
            <section v-if="item && item.slotName">
              <slot :name="item.prop" :scope="scope"></slot>
            </section>
            <section v-else>
              <!-- or -->
              <div v-if="item && item.edit">
                <el-input v-model="scope.row[item.prop]" placeholder="" />
              </div>

              <div v-else>
                {{ scope.row[item.prop] }}
              </div>
            </section>
          </template>
        </el-table-column>
      </el-table>
    </section>

    <!--  -->
    <section class="section-pagination">
      <el-pagination
        v-bind="$attrs"
        :page-size="pageSize"
        :current-page="_pageNo"
        :total="count"
        @current-change="_handleCurrentChange"
      >
      </el-pagination>
    </section>
  </div>
</template>
<script>
// import formCreate from '@form-create/element-ui';
export default {
  name: 'AwesomeTableList',
  // components: { formCreate },
  props: {
    /**
     * 
     *   http://www.form-create.com/v2/guide/rule.html
     * json
     * 
     */
    formRules: {
      required: true,
      type: Array,
      default: () => [],
    },
    formOptions: {
      required: false,
      type: Object,
      default: () => {
        return {
          form: {
            inline: true,
            labelPosition: 'left',
            labelWidth: '100px',
          },
          submitBtn: {
            show: false,
          }, // form-create 
        };
      },
    },

    /**  */
    isShowStatistics: {
      required: false,
      type: Boolean,
      default: true,
    },

    /**  */
    selection: {
      required: false,
      type: Boolean,
      default: false,
    },
    tableData: {
      required: true,
      type: Array,
      default: () => [],
    },
    columns: {
      required: true,
      type: Array,
      default: () => [],
    },

    /**  */
    pageNo: {
      required: false,
      type: Number,
      default: 1,
    },
    pageSize: {
      required: false,
      type: Number,
      default: 10,
    },
    count: {
      required: true,
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      fApi: {},
    };
  },
  computed: {
    _pageNo() {
      return this.pageNo;
    },
  },
  created() {},
  methods: {
    // 
    _handleSearch() {
      this.$emit('handle-search', this.getCurrentFormData());
    },
    // 
    _handleReset() {
      this.fApi.resetFields(); //  
      this.$emit('reset-search', this.getCurrentFormData());
    },
    // 
    _handleCurrentChange(pageNo) {
      this.$emit('page-change', {
        pageNo,
        formData: this.getLastFormData(),
      });
    },
    // form 
    getCurrentFormData() {
      return this.fApi.form;
    },
    // form 
    getLastFormData() {
      return this.fApi.formData();
    },
    // form-create 
    getFApi() {
      return this.fApi;
    },
  },
};
</script>

<style lang="postcss" scoped>
/* ------------------toolbar------------------- */
.toolbar-panel .toolbar-item {
  display: inline-block;
  padding-right: 10px;
  margin-bottom: 12px;
  vertical-align: middle;
}

.toolbar-panel .toolbar-item.toolbar-prefix-item {
  padding-right: 0;
}
.toolbar-panel .el-input__inner {
  height: 33px;
}

.toolbar-panel .toolbar-item .el-input {
  vertical-align: middle;
}

.toolbar-panel .toolbar-item .el-input {
  width: 220px;
  vertical-align: middle;
}

.toolbar-panel .toolbar-item .el-select {
  width: 120px;
  .el-input {
    width: 100%;
  }
}

.toolbar-panel .toolbar-item .item-label,
.toolbar-panel .toolbar-item .item-label,
.toolbar-panel .toolbar-item .el-select {
  vertical-align: middle;
  padding-right: 5px;
}
/* ------------------toolbar------------------- */
.toolbar-totaltip {
  height: 28px;
  line-height: 30px;
  padding-left: 10px;
  margin-top: 8px;
  font-size: 13px;
  background: #fff;
  border: 1px solid #009e96;
}
.section-pagination {
  margin-top: 20px;
}
</style>
